@-webkit-keyframes ticker {
  0% {
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
    visibility: visible;
  }
  100% {
    -webkit-transform: translate3d(-100%, 0, 0);
    transform: translate3d(-100%, 0, 0);
  }
}
@keyframes ticker {
  0% {
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
    visibility: visible;
  }
  100% {
    -webkit-transform: translate3d(-100%, 0, 0);
    transform: translate3d(-100%, 0, 0);
  }
}
.ticker-wrap {
  width: 100%;
  bottom: 0;
  background-color: rgba(20, 24, 28, 0.9);
  padding-left: 100%;
  height: 100%;
  overflow: hidden;
}
.ticker-wrap:hover .ticker {
  -webkit-animation-play-state: paused;
  -moz-animation-play-state: paused;
  -o-animation-play-state: paused;
  animation-play-state: paused;
}
.ticker-wrap .ticker {
  height: 100%;
  display: inline-flex;
  white-space: nowrap;
  padding-right: 100%;
  box-sizing: content-box;
  -moz-animation-timing-function: linear;
  -webkit-animation-timing-function: linear;
  animation-timing-function: linear;
  -moz-animation-iteration-count: infinite;
  -webkit-animation-iteration-count: infinite;
  animation-iteration-count: infinite;
  -moz-animation-name: ticker;
  -webkit-animation-name: ticker;
  animation-name: ticker;
}
.ticker-wrap .ticker__headline {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0.6rem;
  color: white;
  height: 100%;
}
.ticker-wrap .fixture-match {
  display: flex;
}
.ticker-wrap .fixture-match .home-team,
.ticker-wrap .fixture-match .away-team {
  position: relative;
  display: flex;
  gap: 0.7rem;
  justify-content: center;
  margin: 0 10px;
  cursor: pointer;
}
.ticker-wrap .fixture-match .home-team:hover,
.ticker-wrap .fixture-match .away-team:hover {
  text-decoration: underline;
}
.ticker-wrap .fixture-match .home-team span,
.ticker-wrap .fixture-match .away-team span {
  pointer-events: none;
}

.modal-content {
  max-height: 600px !important;
}
.modal-header {
  max-height: 100px;
}
.modal--body, .modal .roster {
  max-height: 500px;
}

.roster {
  display: flex;
  flex-wrap: wrap;
  overflow-y: scroll;
  overflow-x: hidden;
  height: 100%;
}
.roster .player-data .player-info {
  padding: 1rem;
}